.table>tbody>tr>td {
    vertical-align: middle;
}

@media print {

    .no-print,
    .no-print * {
        display: none !important;
    }

    table {
        page-break-after: auto
    }

    tr {
        page-break-inside: avoid;
        page-break-after: auto
    }

    td {
        page-break-inside: avoid;
        page-break-after: auto
    }

    thead {
        display: table-header-group
    }

    tfoot {
        display: table-footer-group
    }

}
